x 0 0px);\n}\n\n.section-btns {\n\t@include grid(60px, auto auto);\n\tjustify-content: left;\n}\n\n.section-button {\n\t@include btn(14px, $accent, $accent, #fff, 15px 30px);\n\tborder-radius: 50px;\n}\n\n.section-buttonWhite {\n\t@include btn(14px, #fff, #fff, $forButton, 15px 30px);\n\tborder-radius: 50px;\n}\n\n.section-buttonToolbar {\n\t@include btn(14px, #ffffff00, #ffffff, #ffffff, 8px 30px);\n\tborder-radius: 5px;\n\tmax-width: 200px;\n\toverflow: hidden;\n}\n\n.section-buttonFooter {\n\t@include btn(14px, #ffffff00, $forButton, $forButton, 8px 30px);\n\tborder-radius: 5px;\n}\n\n.section_h1 {\n\tfont-size: 48px;\n\tfont-weight: 800;\n}\n.section_h2 {\n\tfont-size: 36px;\n\tfont-weight: 800;\n}\n.section_h3 {\n\tfont-size: 24px;\n\tfont-weight: 700;\n}\n.section_h4 {\n\tfont-size: 18px;\n}\n\n.section-button a {\n\tcolor: inherit;\n\tfont-weight: 800;\n}\n.section-button:hover {\n\tcursor: pointer;\n\tbackground: #ffffff00;\n\tcolor: $accent !important;\n}\n.section-buttonWhite:hover {\n\tcursor: pointer;\n\tbackground: $forButton;\n\tborder: $forButton;\n\tcolor: #fff !important;\n}\n.section-buttonToolbar:hover {\n\tcursor: pointer;\n\tbackground: #ffffff;\n\tcolor: $main_text_color !important;\n}\n\n.list {\n\tmargin: 24px 0;\n\tdisplay: grid;\n\tgrid-gap: 20px;\n\tfont-size: 18px;\n\t.list__li {\n\t\tposition: relative;\n\t\tpadding-left: 50px;\n\t\tdisplay: grid;\n\t\talign-content: center;\n\t\tmin-height: 32px;\n\t}\n\t.list__li::before {\n\t\tcontent: '';\n\t\tposition: absolute;\n\t\twidth: 32px;\n\t\tleft: 0;\n\t\theight: 32px;\n\t\tbackground: url('https://www.pulscen.ru/system/ckeditor_assets/pictures/280238/content_group_409.png') no-repeat 100% 100% / cover;\n\t}\n\t.list__li_gray::before {\n\t\tbackground: url('https://www.pulscen.ru/system/ckeditor_assets/pictures/280239/content_group_410.png') no-repeat 100% 100% / cover;\n\t}\n}\n\n.contact__by {\n\t@include grid(15px, auto);\n}\n\n@media (max-width: 1440px) {\n\t.section_h1 {\n\t\tfont-size: 40px;\n\t\tfont-weight: 800;\n\t}\n}\n\n@media (max-width: 660px) {\n\t.section-btns {\n\t\tgrid-template-columns: 1fr;\n\t}\n\t.section-buttonToolbar {\n\t\tpadding: 8px 20px;\n\t\tfont-size: 12px;\n\t}\n\t.logo {\n\t\twidth: 100px;\n\t}\n\t.section_h1 {\n\t\tfont-size: 26px;\n\t}\n\t.section_h2 {\n\t\tfont-size: 24px;\n\t}\n\t.section_h3 {\n\t\tfont-size: 16px;\n\t}\n\t.section_h4 {\n\t\tfont-size: 14px;\n\t}\n\t.list {\n\t\tfont-size: 14px;\n\t}\n}\n","/** @format */\n\n@mixin mainContent($size, $color) {\n\tmax-width: 1920px;\n\twidth: 100%;\n\tmargin: auto;\n\tfont-size: $size;\n\tcolor: $color;\n\tbackground: #f6f6f6;\n\tfont-family: 'Montserrat', sans-serif;\n}\n@mixin content($padd...) {\n\tmax-width: 1400px;\n\twidth: 90%;\n\tmargin: auto;\n\tpadding: $padd;\n}\n\n@mixin grid($gap, $fr...) {\n\tdisplay: grid;\n\tgrid-template-columns: $fr;\n\tgrid-gap: $gap;\n}\n\n@mixin btn($size, $bg, $border, $color, $pad...) {\n\tletter-spacing: 0.06em;\n\tletter-spacing: 0.06em;\n\tfont-size: $size;\n\tbackground: $bg;\n\tborder-radius: 5px;\n\twidth: fit-content;\n\tpadding: $pad;\n\tdisplay: flex;\n\tjustify-content: center;\n\talign-content: center;\n\tcolor: $color !important;\n\ttransition-duration: 0.5s;\n\tborder: 1px solid $border;\n\ttext-align: center;\n}\n","/** @format */\n.toolbar-rascal {\n\tpadding-top: 56px;\n\t.wrapper {\n\t\tdisplay: flex;\n\t\tjustify-content: space-between;\n\t\talign-items: center;\n\t\timg {\n\t\t\tmax-width: 100%;\n\t\t\twidth: 100%;\n\t\t\theight: auto;\n\t\t\tmin-width: 200px;\n\t\t}\n\t}\n\t.text {\n\t\tmax-width: 628px;\n\t\tmargin-right: 30px;\n\t}\n\t.heading {\n\t\tfont-size: 48px;\n\t\tmargin-top: 130px;\n\t\tmax-width: 750px;\n\t}\n\t.offer {\n\t\t@include grid(24px, 1fr);\n\t\tmargin: 80px 0;\n\t\tmax-width: 600px;\n\t}\n\t.choice {\n\t\tp {\n\t\t\tfont-size: 24px;\n\t\t}\n\t}\n\t.toolbar__block {\n\t\tdisplay: flex;\n\t\tjustify-content: space-between;\n\t\talign-items: center;\n\t\t.toolbar__feedback {\n\t\t\t@include grid(15px, auto auto auto);\n\t\t\talign-items: center;\n\t\t}\n\t\t.toolbar__icon {\n\t\t\twidth: 20px;\n\t\t}\n\t}\n\t.numder_toolbar {\n\t\tmargin-left: 10px;\n\t\tmargin-right: 10px;\n\t\tcolor: #fff;\n\t}\n}\n\n@media (max-width: 1200px) {\n\t.toolbar-rascal {\n\t\t.heading {\n\t\t\tfont-size: 36px;\n\t\t}\n\t}\n}\n\n@media (max-width: 900px) {\n\t.toolbar-rascal {\n\t\t.toolbar {\n\t\t\t.toolbar__feedback {\n\t\t\t\tgrid-template-columns: 1fr;\n\t\t\t}\n\t\t\t.massenger_futer {\n\t\t\t\tdisplay: none;\n\t\t\t}\n\t\t}\n\n\t\t.wrapper {\n\t\t\tflex-direction: column-reverse;\n\t\t\talign-items: baseline;\n\t\t\tmargin-top: 40px;\n\t\t}\n\t\t.heading {\n\t\t\tmargin-top: 20px;\n\t\t}\n\t\t.img {\n\t\t\tdisplay: grid;\n\t\t\talign-self: center;\n\t\t\twidth: 200px;\n\t\t}\n\t\t.offer {\n\t\t\tmargin: 24px 0 36px;\n\t\t}\n\t\t.massenger_futer {\n\t\t\tdisplay: none;\n\t\t}\n\t\t.toolbar__block {\n\t\t\t.toolbar__feedback {\n\t\t\t\tgrid-template-columns: auto;\n\t\t\t}\n\t\t}\n\t}\n}\n\n@media (max-width: 800px) {\n\t.toolbar-rascal {\n\t\t.heading {\n\t\t\tfont-size: 24px;\n\t\t}\n\t}\n}\n","/**\n * Swiper 7.0.8\n * Most modern mobile touch slider and framework with hardware accelerated transitions\n * https://swiperjs.com\n *\n * Copyright 2014-2021 Vladimir Kharlampidi\n *\n * Released under the MIT License\n *\n * Released on: October 4, 2021\n */\n\n@font-face{font-family:swiper-icons;src:url('data:application/font-woff;charset=utf-8;base64, ');font-weight:400;font-style:normal}:root{--swiper-theme-color:#007aff}.swiper{margin-left:auto;margin-right:auto;position:relative;overflow:hidden;list-style:none;padding:0;z-index:1}.swiper-vertical>.swiper-wrapper{flex-direction:column}.swiper-wrapper{position:relative;width:100%;height:100%;z-index:1;display:flex;transition-property:transform;box-sizing:content-box}.swiper-android .swiper-slide,.swiper-wrapper{transform:translate3d(0px,0,0)}.swiper-pointer-events{touch-action:pan-y}.swiper-pointer-events.swiper-ve